//更新数据
function updateOption()
						    {
                        var data = window.parent.getIdentityData();
						            var data_r1 = data['普通']
						            var data_r2 = data['橙V（个人）']
						            var data_r3 = data['蓝V（官方）']

                                    
                        var data_max,data_mid,data_min,label_min,label_mid,label_max
                                    if((data_r1 >= data_r2)&(data_r1 >= data_r3)) 
                                        {data_max = data_r1;
                                         if(data_r2 >= data_r3){data_mid = data_r2;data_min = data_r3;}
                                         else{data_mid = data_r3;data_min = data_r2;}
                                        }
                                    if((data_r2 >= data_r1)&(data_r2 >= data_r3)) 
                                        {data_max = data_r2;
                                         if(data_r1 >= data_r3){data_mid = data_r1;data_min = data_r3;}
                                         else{data_mid = data_r3;data_min = data_r1;}
                                        }
                                    if((data_r3 >= data_r1)&(data_r3 >= data_r2)) 
                                        {data_max = data_r3;
                                         if(data_r1 >= data_r2){data_mid = data_r1;data_min = data_r2;}
                                         else{data_mid = data_r2;data_min = data_r1;}
                                        }
                                  

                        var data_min_point = 9.375*data_min+90.625
                        var data_mid_point = 9.375*data_mid+890.625

                        

